<template>
    <div style="height: 100%;">
        <div class="index-d3 switch">
            <div class="d3-more" style="display: block;">
              <h2></h2>
              <details>
                  <summary></summary>
                  <h3></h3>
                  <pre v-highlightjs="code1"><code class="html">{{ code1 }}</code></pre>
                  <h3></h3>
                  <pre v-highlightjs="code2"><code class="html">{{ code2 }}</code></pre>
                </details>
                <div class="team-msg">
                  <div class="msg-dlg-wp">
                    <p class="msg-load"><span class="ico-load"></span>...</p>
                    <a class="btn-more btn-msg" style="display: flex;">
                      <i class="ico-arw-s"></i><span>99+</span>
                      <i class="line">| </i> <i class="close" title=""></i>
                    </a>
                    <msg-item
                      v-for="item in lists"
                      :key="item.userid"
                      :userid="item.userid"
                      :msg="item.msg"
                      :usernick="item.usernick"
                      :time="item.time"
                      :myself="item.myself"
                    ></msg-item>
                  </div>

                  <div class="msg-dlg-input">
                    <textarea class="area-msg-dlg"></textarea>
                    <p class="area-msg-null"><a href="javascript:;"></a></p>
                    <a href="javascript:;" class="btn-msg-send disable"></a>
                  </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import MsgItem from '@/components/MsgItem'
import MsgItemSkeleton from '@/components/MsgItemSkeleton'
import msgLists from '@/assets/json/msg-lists.json'
export default {
    components: {
      MsgItem,
      MsgItemSkeleton
    },

    data () {
      return {
        viewport: null,
        lists: msgLists.data.slice(0, 1000),
        code1: `<div class="team-msg">
  <div class="msg-dlg-wp">
    <p class="msg-load"><span class="ico-load"></span>...</p>
    <a class="btn-more btn-msg" style="display: flex;">
      <i class="ico-arw-s"></i><span>99+</span>
      <i class="line">| </i> <i class="close" title=""></i>
    </a>
    <msg-item
      v-for="item in lists"
      :key="item.userid"
      :userid="item.userid"
      :msg="item.msg"
      :usernick="item.usernick"
      :time="item.time"
      :myself="item.myself"
    ></msg-item>
  </div>
</div>`,
        code2: `<div>
  <p class="msg-time">
    <time>{{ time }}</time>
  </p>
  <div class="msg-dlg-box" :class="myself ? 'myself' : 'other'">
    <a class="user-pic">
      <vue-lazy-component
        :view-port="$parent.$el"
      >
        <img :src="'https://api.adorable.io/avatars/40/'+ userid + '@adorable.png'">
        <img slot="skeleton" src="../assets/img/timg.jpeg">
      </vue-lazy-component>
    </a>

    <div class="other-info" v-if="!myself">
      <p class="user-name">{{ usernick }}</p>
      <span class="level-box level3">
        <i class="ico-level ico-lev3"></i></span>
    </div>
    <div class="bubble">{{ msg }}</div>
  </div>
</div>`
      }
    }
}
</script>

<style>
  .team-msg {
    height: 450px;
    width: 70%;
    margin: 0 15%;
  }

  .msg-dlg-box {
    padding: 0 0 0 45px;
  }

  .msg-dlg-box.myself .bubble {
    margin: 0 45px 0 0;
  }
</style>




